export function removeByIndex(i, list) {
    return map((tuple) => tuple[1], filter((tupledArg) => (i !== tupledArg[0]), indexed(list)));
}

export function update(msg, model) {
    switch (msg.tag) {
        case 1: {
            return [new Model(new State(2), model.Numbers, model.Score, model.HighScore, model.Buffer), Cmd_none()];
        }
        case 2: {
            const i = msg.fields[0] | 0;
            const number = item(i, model.Numbers) | 0;
            return [new Model(model.State, removeByIndex(i, model.Numbers), model.Score, model.HighScore, cons(number, model.Buffer)), Cmd_OfFunc_result(new Msg(3))];
        }
        case 3: {
            if (length(model.Buffer) > 3) {
                return [model, Cmd_OfFunc_result(new Msg(1))];
            }
            else if (sum(model.Buffer, {
                GetZero: () => 0,
                Add: (x, y) => (x + y),
            }) > 10) {
                return [model, Cmd_OfFunc_result(new Msg(1))];
            }
            else if ((length(model.Buffer) === 3) ? (sum(model.Buffer, {
                GetZero: () => 0,
                Add: (x_1, y_1) => (x_1 + y_1),
            }) !== 10) : false) {
                return [model, Cmd_OfFunc_result(new Msg(1))];
            }
            else if (sum(model.Buffer, {
                GetZero: () => 0,
                Add: (x_2, y_2) => (x_2 + y_2),
            }) === 10) {
                const score = (model.Score + (length(model.Buffer) - 1)) | 0;
                return [new Model(model.State, model.Numbers, score, max(comparePrimitives, score, model.HighScore), empty()), Cmd_none()];
            }
            else {
                return [model, Cmd_none()];
            }
        }
        case 5: {
            if (length(model.Numbers) === 10) {
                return [model, Cmd_OfFunc_result(new Msg(1))];
            }
            else {
                return [new Model(model.State, append(model.Numbers, singleton(randomNext(1, 8))), model.Score, model.HighScore, model.Buffer), Cmd_none()];
            }
        }
        case 4: {
            toConsole(printf("ticked"));
            if (model.State.tag === 1) {
                return [model, Cmd_ofSub((dispatch) => {
                    startImmediate(singleton_1.Delay(() => {
                        dispatch(new Msg(5));
                        return singleton_1.Bind(sleep(1000), () => {
                            dispatch(new Msg(4));
                            return singleton_1.Zero();
                        });
                    }));
                })];
            }
            else {
                return [model, Cmd_none()];
            }
        }
        default: {
            return [new Model(new State(1), empty(), 0, model.HighScore, empty()), Cmd_OfFunc_result(new Msg(4))];
        }
    }
}

